.portfolio {
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 20px;

    h1{
        font-size: 80px;
    }

    ul {
        margin: 10px;
        padding: 0;
        list-style: none;
        display: flex;

        li {
            font-size: 20px;
            margin-right: 50px;
            padding: 7px;
            border-radius: 10px;
            cursor: pointer;

            &.active {
                background-color: lightgray;
            }
        }
    }

    .container {
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;

        .item {
            width: 220px;
            height: 150px;
            border-radius: 20px;
            border: 1px solid lightgray;
            margin: 10px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: whitesmoke;
            position: relative;
            transition: all 1s ease;
            cursor: pointer;


            h3 {
                position: absolute;
                font-size: 20px;
                color: black;
            }

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index: 1;
            }

            &:hover {
                background-color: lightgray;
                img{
                    opacity: 0.2;
                    z-index: 0;
                }
            }
        }
    }
}